/* Основные стили для контейнера */
.container {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
  }
  
  /* Заголовок */
  .header {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center;
  }
  
  /* Стиль для инпута */
  .input {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
  }
  
  /* Контейнер для карточек */
  .card-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  /* Стиль для каждой карточки */
  .card {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    background-color: #fafafa;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  /* Контент внутри карточки */
  .card-content {
    font-size: 16px;
    line-height: 1.6;
  }
  
  /* Стиль для названия карточки */
  .card-title {
    font-size: 20px;
    font-weight: bold;
    color: #333;
    margin-bottom: 10px;
  }